import { onMounted, ref } from "vue";
import { User, Monitor, Bell, OfficeBuilding, Brush, Back, Right } from "@element-plus/icons-vue";
import { useStore } from "vuex";
export default {
  name: "dmsaside",
  components: {
    User,
    Monitor,
    Bell,
    OfficeBuilding,
    Brush,
    Back,
    Right
  },
  setup() {
    const store = useStore();
    const isCollapse = ref(false);
    const elmenu = ref(null);
    const handleOpen = () => {
      console.log();
    };
    const handleClose = () => {
      console.log();
    };
    const menuFold = () => {
      if (isCollapse.value == false) {
        const activeItem = document.querySelector(".is-opened");
        if (activeItem) {
          activeItem.firstElementChild.click();
          setTimeout(() => {
            isCollapse.value = true;
          }, 500);
        } else {
          isCollapse.value = true;
        }
      } else {
        isCollapse.value = false;
      }
    };
    const menutype = ref(1);
    onMounted(() => {
      document.querySelector(".el-menu-item").click();
      menutype.value = store.state.dsuser.dsuserInfo.type;
    });
    return {
      isCollapse,
      handleOpen,
      handleClose,
      elmenu,
      menuFold,
      menutype
    };
  }
};